<template>
    <div class = "header" @mouseenter="showLinks = true" @mouseleave="showLinks = false">
        <div class="header_top">
            <h1>
                This is Me (Josh)
            </h1>
        </div>
        <div class="header_bottom" v-if="showLinks">
           <div class = "scrollTop" @click="changePage('aboutme')">About Me</div>
            <div class = "scrollTop" @click="changePage('skills')">Skills</div>
            <div class = "scrollTop" @click ="changePage('project_container')">My Projects</div>
            <div class = "scrollTop" @click ="changePage('contact')">Contact Me</div>
        </div>
    </div>
</template>

<script>
export default 
{
  name: 'Header',
  data()
  {
      return{
          showLinks:false,
      };
  },
  props:
  {
      changePage:Function,
  },
  methods:
  {
      scroll(clas)
      {
          document.getElementsByClassName(clas)[0].scrollIntoView({behavior:"smooth"})
      }
  }
}
</script>
   
<style scoped>
 .header
    {       
        background: white;
        box-shadow:1px 1px 1px 1px grey;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        z-index: 500;
        position: sticky;
        top:0px;
        text-align: center;
    }

    h1
    {
        text-transform: uppercase;
    }
    
    .header_bottom
    {
        display: flex;
        flex-direction: row;
        justify-content:space-evenly;
        animation-name: sc;
        animation-duration: .5s;
        animation-play-state: running;
    }

    @keyframes sc
    {
        from
        {
            flex-basis: 0%;
        }

        to
        {
            flex-basis: 100%;
        }
    }

    .scrollTop
    {
        text-decoration: underline;
        font-size:20px;

    }

    .scrollTop:hover
    {
        cursor:pointer;
        color:blue;
        transform:scale(1.1)
    }
</style>
