<template>
    <div class = "skill">
        <h2  >{{title}}</h2>
        <div class = "skill_value" >
            <img src = "../assets/gold_star.png" class = 'star' v-for="i in value" :key="i"/>
            <img src = "../assets/star.png" class = 'star' v-for="i in (10 - value)" :key="i"/>        
        </div>
    </div>
</template>
<script>
export default {
    name:"Skill",
    props:
    {
        title:String,
        value:Number,
    }
}
</script>
<style>
    .skill
    {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;        
    }
    .skill_title
    {
        width:inherit;
    }

    .skill_value
    {
        display: flex;
        width:100%;
        column-gap: 10px;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: center;
    }
    .star
    {        
        width:20px;
        height:20px;
        background-size: cover;
        transition-duration: .25s;
    }

    .star:hover
    {
        transform:scale(1.25)
    }
</style>