<template>   
    <div v-if="link || title"  class="project"  @mouseenter="hover = true" @mouseleave="hover = false">        
       <a :href = "link" :title = "link&& 'Click Me'" target = "blank"> <h2 class="project_title" >{{title}}</h2></a>
        <div class = "info_holder">
            <div>{{getProps()[slide].toUpperCase()}}</div>
            <div class = "createdWith" v-if = "createdWith && getProps()[slide] == 'created with'">
                <li style = "text-align:center; padding:0px" v-for="(c,i) in createdWith" :key="i">{{c}}</li>
            </div>
             <p v-if = "description && getProps()[slide] == 'description'" class="project_description">{{description}}</p>
             <iframe v-if = "link  && getProps()[slide] == 'demo'" :src="link"/>
             <img  v-if = 'image && hover' class="project_image" :src= "img"/>   
        </div>      
        <div class = "nPage" v-if= 'getProps().length > 1'>
            <button @click="nextPage(-1)"></button>
            <button @click="nextPage(1)"></button>
        </div>    
    </div>
</template>

<script>
    export default
    {
        name:'Project',
        props:
        {
            img:String,
            title:String,
            description:String,
            link:String,
            createdWith:[]
        },
        data()
        {
            
            return{
                hover:false,
                slide:0
            }
        },
        methods: 
        {
            getProps()
            {
                let props = [];
                if(this.img)
                {
                    props.push("Image")
                }
                if(this.description)
                {
                    props.push("description")
                }
                if(this.createdWith)
                {
                    props.push("created with")
                }
                if(this.link)
                {
                    props.push("demo")
                }


                return props;
            },
            isMobile() 
            {
                if( screen.width <= 760 ) {
                    return true;
                }
                else {
                    return false;
                }
            },
            nextPage(num)
            {
                this.slide+=num;
                if(this.slide >= this.getProps().length)
                {
                    this.slide = 0;
                }
                if(this.slide < 0)
                {
                    this.slide = this.getProps().length - 1;
                }
            }
        }
    }
</script>

<style scoped>

    h3
    {
        color:white;
        margin:0px;
        text-align: center;
    }

    .info_holder
    {
        flex-basis: 75%;   
        width:100%;     
        overflow: auto;
        color:white;
    }

    .project
    {
        position:relative;
        text-decoration:none;
        width:min(500px, 95vw - 10px);
        height:min(350px, 75vw);
        border-radius:10px;
        box-shadow:2px 2px grey;
        display: flex;
        flex-direction: column;
        background: linear-gradient(to bottom, black, black);
        padding:10px;
        transition-duration: 1s;
        overflow:hidden;
        row-gap: 5px;      
    
    }
    .project:hover 
    {
        transform: scale(1.1);
        cursor:pointer;
        box-shadow: 2px 2px 2px 2px black; 

    }
    .project_title
    {        
        text-align: center;
        flex-basis: 10%;
        text-decoration:none;   
        color:aliceblue     
    }
    .createdWith
    {
        display:flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;
        row-gap: 0px;
        color:white;
        
    }
    .project_description
    {
        color:white;
        font-size: 20px;
    }

    .project_description::before
    {
        content:"\00a0 ";
    }

    .nPage
    {
        display:flex; 
        flex-basis:15%;
        justify-content:space-evenly;
        bottom:0px; 
        width:100%;
        left:0px;
        overflow:hidden;
        
    }

    .nPage>button
    {
        opacity: .75;
        flex-basis: 50%;
    }
    .nPage>button:first-of-type
    {        
        background:lightblue;
    }

    .nPage>button:last-of-type
    {
        background:white;
        
    }
    iframe
    {
        width:100%;
        height:100%;
    }

    .nPage>button:hover
    {
        opacity: 1;
        cursor:pointer;
    }
</style>